import React from 'react';
import { useTheme } from '@/contexts/ThemeContext';
import { useFatigueSettings } from '@/contexts/FatigueSettingsContext';
import { DashboardLayout } from '@/components/DashboardLayout';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Input } from '@/components/ui/input';
import { Moon, Sun, Laptop, Save, Zap, RotateCcw, Activity } from 'lucide-react';
import { Separator } from '@/components/ui/separator';
import { cn } from '@/lib/utils';

const SettingsPage = React.memo(() => {
  const { theme, setTheme } = useTheme();
  const { settings, updateSettings, resetToDefaults } = useFatigueSettings();
  const [cacheDuration, setCacheDuration] = React.useState('7');

  return (
    <DashboardLayout>
      <div className="flex flex-col gap-8 max-w-3xl mx-auto">
        <div>
          <h2 className="text-3xl font-bold tracking-tight mb-2 bg-gradient-to-r from-primary to-accent bg-clip-text text-transparent">Settings</h2>
          <p className="text-muted-foreground">Manage your application preferences</p>
        </div>
        
        <div className="grid gap-6">
          {/* Appearance Settings */}
          <Card className="glass-card">
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <div className="space-y-1.5">
                  <CardTitle className="text-xl font-semibold bg-gradient-to-r from-foreground to-foreground/70 bg-clip-text text-transparent">
                    Appearance
                  </CardTitle>
                  <CardDescription>
                    Customize how the application looks
                  </CardDescription>
                </div>
                <div className="flex h-10 w-10 items-center justify-center rounded-full bg-gradient-to-br from-primary/80 to-accent/80 text-white shadow-sm">
                  <Sun className="h-5 w-5" />
                </div>
              </div>
            </CardHeader>
            <CardContent className="space-y-6 pt-6">
              <div className="space-y-4">
                <div>
                  <Label className="text-sm font-medium mb-3 block">Theme Mode</Label>
                  <div className="grid grid-cols-3 gap-3">
                    <Button 
                      variant={theme === "light" ? "default" : "outline"} 
                      className={cn(
                        "flex h-24 flex-col items-center justify-center gap-2 rounded-lg px-4 py-3 transition-all duration-300",
                        theme === "light" ? "bg-gradient-to-br from-primary to-accent text-primary-foreground shadow-md" : ""
                      )}
                      onClick={() => setTheme("light")}
                    >
                      <Sun className="h-6 w-6" />
                      <span>Light</span>
                    </Button>
                    <Button 
                      variant={theme === "dark" ? "default" : "outline"} 
                      className={cn(
                        "flex h-24 flex-col items-center justify-center gap-2 rounded-lg px-4 py-3 transition-all duration-300",
                        theme === "dark" ? "bg-gradient-to-br from-primary to-accent text-primary-foreground shadow-md" : ""
                      )}
                      onClick={() => setTheme("dark")}
                    >
                      <Moon className="h-6 w-6" />
                      <span>Dark</span>
                    </Button>
                    <Button 
                      variant={theme === "system" ? "default" : "outline"} 
                      className={cn(
                        "flex h-24 flex-col items-center justify-center gap-2 rounded-lg px-4 py-3 transition-all duration-300",
                        theme === "system" ? "bg-gradient-to-br from-primary to-accent text-primary-foreground shadow-md" : ""
                      )}
                      onClick={() => setTheme("system")}
                    >
                      <Laptop className="h-5 w-5" />
                      <span className="text-xs">System</span>
                    </Button>
                  </div>
                  <p className="text-xs text-muted-foreground mt-2">
                    Choose a theme preference or use your system settings
                  </p>
                </div>
                <Separator />
              </div>
            </CardContent>
          </Card>

          {/* Cache Settings */}
          <Card className="glass-card">
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <div className="space-y-1.5">
                  <CardTitle className="text-xl font-semibold bg-gradient-to-r from-foreground to-foreground/70 bg-clip-text text-transparent">
                    Cache Settings
                  </CardTitle>
                  <CardDescription>
                    Configure how workout data is stored locally
                  </CardDescription>
                </div>
                <div className="flex h-10 w-10 items-center justify-center rounded-full bg-gradient-to-br from-primary/80 to-accent/80 text-white shadow-sm">
                  <svg className="h-5 w-5" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><circle cx="12" cy="12" r="10"/><polyline points="12 6 12 12 16 14"/></svg>
                </div>
              </div>
            </CardHeader>
            <CardContent className="space-y-6 pt-6">
              <div className="space-y-4">
                <div>
                  <Label htmlFor="cacheDuration" className="text-sm font-medium mb-2 block">Cache Duration</Label>
                  <Select
                    value={cacheDuration}
                    onValueChange={setCacheDuration}
                  >
                    <SelectTrigger className="w-full sm:w-[240px]">
                      <SelectValue placeholder="Select duration" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="1">1 day</SelectItem>
                      <SelectItem value="3">3 days</SelectItem>
                      <SelectItem value="7">7 days</SelectItem>
                      <SelectItem value="14">14 days</SelectItem>
                      <SelectItem value="30">30 days</SelectItem>
                    </SelectContent>
                  </Select>
                  <p className="text-xs text-muted-foreground mt-2">
                    Data older than this will be refreshed from the server when you're online
                  </p>
                </div>
              </div>
            </CardContent>
            <CardFooter className="bg-muted/50 border-t">
              <Button 
                variant="default"
                className="ml-auto bg-gradient-to-r from-primary to-accent hover:from-primary/90 hover:to-accent/90 text-primary-foreground"
              >
                <Save className="h-4 w-4 mr-2" />
                Save Settings
              </Button>
            </CardFooter>          </Card>

          {/* Muscle Fatigue Settings */}
          <Card className="glass-card">
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <div className="space-y-1.5">
                  <CardTitle className="text-xl font-semibold bg-gradient-to-r from-foreground to-foreground/70 bg-clip-text text-transparent">
                    Muscle Fatigue Settings
                  </CardTitle>
                  <CardDescription>
                    Customize fatigue calculation and recovery parameters
                  </CardDescription>
                </div>
                <div className="flex h-10 w-10 items-center justify-center rounded-full bg-gradient-to-br from-primary/80 to-accent/80 text-white shadow-sm">
                  <Activity className="h-5 w-5" />
                </div>
              </div>
            </CardHeader>
            <CardContent className="space-y-6 pt-6">
              <div className="grid gap-6">
                {/* Days to Consider */}
                <div className="space-y-2">
                  <Label htmlFor="daysToConsider" className="text-sm font-medium">
                    Days to Consider for Fatigue
                  </Label>
                  <Input
                    id="daysToConsider"
                    type="number"
                    min="1"
                    max="30"
                    value={settings.daysToConsider}
                    onChange={(e) => updateSettings({ daysToConsider: parseInt(e.target.value) || 7 })}
                    className="w-full sm:w-[120px]"
                  />
                  <p className="text-xs text-muted-foreground">
                    Number of recent days to analyze for muscle fatigue calculation
                  </p>
                </div>

                <Separator />

                {/* Recovery Rate */}
                <div className="space-y-2">
                  <Label htmlFor="recoveryRate" className="text-sm font-medium">
                    Recovery Rate (% per day)
                  </Label>
                  <Input
                    id="recoveryRate"
                    type="number"
                    min="5"
                    max="50"
                    step="0.5"
                    value={settings.recoveryRate}
                    onChange={(e) => updateSettings({ recoveryRate: parseFloat(e.target.value) || 15 })}
                    className="w-full sm:w-[120px]"
                  />
                  <p className="text-xs text-muted-foreground">
                    How much fatigue reduces per day of rest (higher = faster recovery)
                  </p>
                </div>

                <Separator />

                {/* Fatigue Thresholds */}
                <div className="space-y-4">
                  <Label className="text-sm font-medium">Fatigue Level Thresholds</Label>
                  
                  <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor="thresholdLow" className="text-xs font-medium text-green-600 dark:text-green-400">
                        Ready to Train
                      </Label>
                      <Input
                        id="thresholdLow"
                        type="number"
                        min="0"
                        max="100"
                        value={settings.thresholds.low}
                        onChange={(e) => updateSettings({ 
                          thresholds: { 
                            ...settings.thresholds, 
                            low: parseInt(e.target.value) || 20 
                          } 
                        })}
                        className="w-full"
                      />
                    </div>
                    
                    <div className="space-y-2">
                      <Label htmlFor="thresholdMedium" className="text-xs font-medium text-yellow-600 dark:text-yellow-400">
                        Recovering
                      </Label>
                      <Input
                        id="thresholdMedium"
                        type="number"
                        min="0"
                        max="100"
                        value={settings.thresholds.medium}
                        onChange={(e) => updateSettings({ 
                          thresholds: { 
                            ...settings.thresholds, 
                            medium: parseInt(e.target.value) || 60 
                          } 
                        })}
                        className="w-full"
                      />
                    </div>
                    
                    <div className="space-y-2">
                      <Label htmlFor="thresholdHigh" className="text-xs font-medium text-red-600 dark:text-red-400">
                        Needs Rest
                      </Label>
                      <Input
                        id="thresholdHigh"
                        type="number"
                        min="0"
                        max="100"
                        value={settings.thresholds.high}
                        onChange={(e) => updateSettings({ 
                          thresholds: { 
                            ...settings.thresholds, 
                            high: parseInt(e.target.value) || 80 
                          } 
                        })}
                        className="w-full"
                      />
                    </div>
                  </div>
                  
                  <p className="text-xs text-muted-foreground">
                    Fatigue levels above these thresholds will show different recovery statuses
                  </p>
                </div>

                <Separator />

                {/* Maximum Fatigue */}
                <div className="space-y-2">
                  <Label htmlFor="maxFatigue" className="text-sm font-medium">
                    Maximum Fatigue Value
                  </Label>
                  <Input
                    id="maxFatigue"
                    type="number"
                    min="50"
                    max="200"
                    value={settings.maxFatigue}
                    onChange={(e) => updateSettings({ maxFatigue: parseInt(e.target.value) || 100 })}
                    className="w-full sm:w-[120px]"
                  />
                  <p className="text-xs text-muted-foreground">
                    Maximum fatigue level that can be accumulated
                  </p>
                </div>
              </div>
            </CardContent>
            <CardFooter className="bg-muted/50 border-t justify-between">
              <Button 
                variant="outline"
                onClick={resetToDefaults}
                className="flex items-center gap-2"
              >
                <RotateCcw className="h-4 w-4" />
                Reset to Defaults
              </Button>
              <Button 
                variant="default"
                className="bg-gradient-to-r from-primary to-accent hover:from-primary/90 hover:to-accent/90 text-primary-foreground"
              >
                <Save className="h-4 w-4 mr-2" />
                Settings Auto-Save
              </Button>
            </CardFooter>
          </Card>
        </div>
      </div>
    </DashboardLayout>
  );
});

SettingsPage.displayName = 'SettingsPage';

export default SettingsPage;